<template>
  <div class="site-nav" :class="{ on: navToggle, off: !navToggle }">
    <b class="nav-mask" @click="navTrigger"></b>
    <div class="nav-con">
      <div class="nav-list">
        <ul class="">
          <li><router-link :to="{ name: 'Home' }" @click.native="navTrigger"><i></i>Home</router-link></li>
          <li><router-link :to="{ name: 'AboutUs' }" @click.native="navTrigger"><i></i>About</router-link></li>
          <li><router-link :to="{ name: 'products' }" @click.native="navTrigger"><i></i>Products</router-link></li>
          <!--
          <li>
            <a href="javascript:;" @click="navExtendTrigger"><i></i>About Us</a>
            <ul class="">
              <li>
                <a href="javascript:;" @click="navExtendTrigger"><i></i>A1</a>
                <ul class="">
                  <li><a href="javascript:;">A11</a></li>
                  <li><a href="javascript:;">A11</a></li>
                </ul>
              </li>
              <li><a href="javascript:;">A2</a></li>
              <li><a href="javascript:;">A3</a></li>
            </ul>
          </li>
          -->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navToggle: 0
    }
  },
  mounted() {
    this.$eventBus.$on('NavToggle', (v) => {
      this.navToggle = v
    })
  },
  methods: {
    navTrigger() {
      this.$eventBus.$emit('NavToggle', 0)
    },
    navExtendTrigger(e) {
      let nextEle = e.currentTarget.nextElementSibling
      this.$utils.hasClass(nextEle, 'on') ? this.$utils.removeClass(nextEle, 'on') : this.$utils.addClass(nextEle, 'on')
    }
  },
}
</script>

<style lang="scss" scoped>

  .site-nav {position:fixed;top:.5rem;left:0;right:0;bottom:0;z-index:100;transform:translateX(100%);
    &.on {transform:translateX(0);
      .nav-mask {opacity:.5;}
      .nav-con {transform:translateX(0);}
    }
    &.off {transform:translateX(100%);transition-delay:.6s;}
    .nav-mask {position:absolute;top:0;left:0;right:0;bottom:0;z-index:1;background-color:#000;opacity:0;transition:opacity .6s;}
    .nav-con {position:absolute;top:0;right:0;bottom:0;width:90vw;z-index:2;background-color:#fff;background-color:#fafafa;transform:translateX(100%);transition:transform .3s;
      .nav-list {
        ul li > a {display:block;padding:.1rem .1rem;line-height:1.5;font-size:.16rem;display:flex;align-items:center;
          i {width:.3rem;height:.2rem;display:flex;justify-content:center;align-items:center;
            &:before {content:'';width:.04rem;height:.04rem;margin-left:-.04rem;overflow:hidden;border-top:.02rem solid #bbb;border-right:.02rem solid #bbb;border-bottom:.02rem solid transparent;border-left:.02rem solid transparent;transform:rotate(45deg);}
          }
        }
        ul li ul {padding-left:.2rem;height:0;overflow:hidden;transition:all .3s;
          &.on {height:auto;}
        }
      }
    }
  }

</style>
